import {Injectable} from '@angular/core';
import {HttpClient, HttpParams, HttpResponse} from '@angular/common/http';
import {AuthService} from './auth.service';
import {map, tap} from 'rxjs/operators';
import {Urls} from '../utils/urls';
import {Router} from '@angular/router';

@Injectable({
    providedIn: 'root'
})
export class ConsultsService {

    constructor(private http: HttpClient, private auth: AuthService, private router: Router) {
    }

    public openConsult(time) {
        const params = new HttpParams()
            .set('token', this.auth.getToken())
            .set('time', time.toString());
        const headers = {
            'Content-Type': 'application/x-www-form-urlencoded'
        };
        return this.http.post(Urls.url('api/openConsult'), params.toString(), {
            headers
        });
    }

    public hasOpenConsult() {
        const params = new HttpParams()
            .set('token', this.auth.getToken());
        const headers = {
            'Content-Type': 'application/x-www-form-urlencoded'
        };
        return this.http.post(Urls.url('api/hasConsult'), params.toString(), {
            headers
        }).pipe(tap(res => {
            if (res['status'] !== 'ok') {
                this.auth.invalidSession();
            }
        }));
    }

    public getGroups() {
        const params = new HttpParams()
            .set('token', this.auth.getToken());
        const headers = {
            'Content-Type': 'application/x-www-form-urlencoded'
        };
        return this.http.post(Urls.url('api/getGroups'), params.toString(), {
            headers
        }).pipe(tap(res => {
            if (res['status'] !== 'ok') {
                this.auth.invalidSession();
            }
        }));
    }

    public addStudentToGroup(name, surname, group_id) {
        const params = new HttpParams()
            .set('token', this.auth.getToken())
            .set('name', name)
            .set('surname', surname)
            .set('group_id', group_id);
        const headers = {
            'Content-Type': 'application/x-www-form-urlencoded'
        };
        return this.http.post(Urls.url('api/addStudent'), params.toString(), {
            headers
        }).pipe(tap(res => {
            if (res['status'] === 'auth_fail') {
                this.auth.invalidSession();
            }
        }));
    }

    public addStudentWithGroup(name, surname, group_name) {
        const params = new HttpParams()
            .set('token', this.auth.getToken())
            .set('name', name)
            .set('surname', surname)
            .set('group_name', group_name);
        const headers = {
            'Content-Type': 'application/x-www-form-urlencoded'
        };
        return this.http.post(Urls.url('api/addStudent'), params.toString(), {
            headers
        }).pipe(tap(res => {
            if (res['status'] === 'auth_fail') {
                this.auth.invalidSession();
            }
        }));
    }

    public getStudentInGroup(group_id) {
        const params = new HttpParams()
            .set('token', this.auth.getToken())
            .set('group_id', group_id);
        const headers = {
            'Content-Type': 'application/x-www-form-urlencoded'
        };
        return this.http.post(Urls.url('api/getGroupStudent'), params.toString(), {
            headers
        }).pipe(tap(res => {
            if (res['status'] !== 'ok') {
                this.auth.invalidSession();
            }
        }));
    }


    public addStudentInGroup(student_id) {
        const params = new HttpParams()
            .set('token', this.auth.getToken())
            .set('student_id', student_id);
        const headers = {
            'Content-Type': 'application/x-www-form-urlencoded'
        };
        return this.http.post(Urls.url('api/addExisttudent'), params.toString(), {
            headers
        }).pipe(tap(res => {
            if (res['status'] === 'auth_fail') {
                this.auth.invalidSession();
            }
        }));
    }

    public getStudentInConsult() {
        const params = new HttpParams()
            .set('token', this.auth.getToken());
        const headers = {
            'Content-Type': 'application/x-www-form-urlencoded'
        };
        return this.http.post(Urls.url('api/getStudentInConsult'), params.toString(), {
            headers
        }).pipe(tap(res => {
            if (res['status'] === 'auth_fail') {
                this.auth.invalidSession();
            }
        }));
    }


    public removeStudentFromConsult(student_id) {
        const params = new HttpParams()
            .set('token', this.auth.getToken())
            .set('student_id', student_id);
        const headers = {
            'Content-Type': 'application/x-www-form-urlencoded'
        };
        return this.http.post(Urls.url('api/removeStudentFromConsult'), params.toString(), {
            headers
        }).pipe(tap(res => {
            if (res['status'] === 'auth_fail') {
                this.auth.invalidSession();
            }
        }));
    }


    public closeCurrentConsult() {
        const params = new HttpParams()
            .set('token', this.auth.getToken());
        const headers = {
            'Content-Type': 'application/x-www-form-urlencoded'
        };
        return this.http.post(Urls.url('api/closeCurrentConsult'), params.toString(), {
            headers
        }).pipe(tap(res => {
            if (res['status'] === 'auth_fail') {
                this.auth.invalidSession();
            }
        }));
    }
}






















