import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {ConsultsService} from '../../../servises/consults.service';

@Component({
    selector: 'app-add-student',
    templateUrl: './add-student.component.html',
    styleUrls: ['./add-student.component.scss']
})
export class AddStudentComponent implements OnInit {
    @Output() loading = new EventEmitter<boolean>();
    @Output() close = new EventEmitter<boolean>();
    @Output() addStudent = new EventEmitter<boolean>();
    old = true;
    groups = [];
    students = [];
    newGroup = false;

    newStudentData = {
        name: '',
        surname: ''
    };
    newGroupName = '';

    selectedGroupId = -1;
    selectedStudentId = -1;

    constructor(private consultsSevice: ConsultsService) {

    }

    ngOnInit() {
        this.onLoadGroups();
    }

    onAddNewStudent() {
        if (!this.newStudentData.name || !this.newStudentData.surname) {
            alert('add all fields');
            return;
        }

        if (!this.newGroup) {
            this.consultsSevice.addStudentToGroup(
                this.newStudentData.name,
                this.newStudentData.surname,
                this.selectedGroupId
            ).subscribe(res => {
                console.log(res);
                this.addStudent.emit(true);
                this.closeWindow();


            });
        } else {
            this.consultsSevice.addStudentWithGroup(
                this.newStudentData.name,
                this.newStudentData.surname,
                this.newGroupName
            ).subscribe(res => {
                console.log(res);
                this.addStudent.emit(true);
                this.closeWindow();
                this.loading.emit(false);

            });
        }
    }

    onLoadGroups() {

        this.consultsSevice.getGroups()
            .subscribe(res => {
                console.log(res);
                this.groups = res['groups'];
                if (this.groups.length > 0) {
                    this.selectedGroupId = Number(this.groups[0]['id']);
                    this.onGroupChange();
                } else {
                    this.selectedGroupId = -1;
                }

            });
    }

    onGroupChange() {

        this.consultsSevice.getStudentInGroup(this.selectedGroupId)
            .subscribe(res => {
                console.log(res['students']);
                this.students = res['students'];
                if (this.students.length > 0) {
                    this.selectedStudentId = Number(this.students[0]['id']);
                } else {
                    this.selectedStudentId = -1;
                }

            });
    }

    closeWindow() {
        this.close.emit(true);
    }

    onAddExistStudent() {
        if (this.selectedStudentId === -1) {
            alert('first join the student or addeted new student');
            return;
        }
        this.loading.emit(true);
        this.consultsSevice.addStudentInGroup(this.selectedStudentId)
            .subscribe(res => {
                this.addStudent.emit(true);
                console.log(res);
                this.loading.emit(false);
                if (res['status'] !== 'ok') {
                    alert('only one student on this consult');
                    return;
                }

                this.closeWindow();


            });
    }
}
