import {BrowserModule} from '@angular/platform-browser';
import {NgModule} from '@angular/core';
import {FormsModule} from '@angular/forms';
import {RouterModule} from '@angular/router';

import {AppComponent} from './app.component';
import {AuthComponent} from './auth/auth.component';
import {HomeComponent} from './home/home.component';


import {AuthGuard} from './servises/auth.guard';
import {HttpClientModule} from '@angular/common/http';
import {MenuComponent} from './menu/menu.component';
import {ConsultComponent} from './home/consult/consult.component';
import {HistoryComponent} from './home/history/history.component';
import {LoaderComponent} from './loader/loader.component';
import {AddStudentComponent} from './home/consult/add-student/add-student.component';
import {AuthService} from './servises/auth.service';
import {NoAuthGuard} from './servises/no-auth.guard';

const homeChildren = [
    {path: '', component: ConsultComponent},
    {path: 'history', component: HistoryComponent},
];
const routes = [
    {path: '', component: HomeComponent, canActivate: [AuthGuard], children: homeChildren},
    {path: 'auth', component: AuthComponent, canActivate: [NoAuthGuard]},
];

@NgModule({
    declarations: [
        AppComponent,
        AuthComponent,
        HomeComponent,
        MenuComponent,
        ConsultComponent,
        HistoryComponent,
        LoaderComponent,
        AddStudentComponent
    ],
    imports: [
        BrowserModule,
        FormsModule,
        RouterModule.forRoot(routes),
        HttpClientModule
    ],
    providers: [AuthGuard, NoAuthGuard, AuthService],
    bootstrap: [AppComponent]
})
export class AppModule {
}
