import {ChangeDetectorRef, Component, OnInit} from '@angular/core';
import {ConsultsService} from '../../servises/consults.service';
import {map, tap} from 'rxjs/operators';

@Component({
    selector: 'app-consult',
    templateUrl: './consult.component.html',
    styleUrls: ['./consult.component.scss']
})
export class ConsultComponent implements OnInit {
    time = '';
    private date = new Date();
    loading = true;
    isOpen = false;
    addingStudent = false;
    students = [];

    constructor(private consultServise: ConsultsService, private cdRef: ChangeDetectorRef) {
    }


    ngOnInit() {
        this.init();
        this.onGetStudentInConsult();
        this.loading = false;
    }

    init() {
        this.consultServise.hasOpenConsult()
            .subscribe((res) => {
                console.log(res);
                if (res['consults'].length > 0) {

                    this.isOpen = true;
                    this.date = new Date(parseInt(res['consults'][0]['starttime'], 10));
                } else {

                    this.isOpen = false;
                    this.date = new Date();
                    this.time = '';
                }
                this.time = this.date.toLocaleString();

            });
    }

    onOpenConsult() {
        this.consultServise.openConsult(this.date.getTime())
            .subscribe(res => {
                console.log(res);
                this.init();
            });
    }

    onUpdateStudentsList() {
        this.onGetStudentInConsult();
    }

    onAddSudent() {
        this.addingStudent = true;
        // this.loading = true;
    }

    onLoadingChange(loading: boolean) {
        this.loading = loading;
        this.cdRef.detectChanges();
    }

    onGetStudentInConsult() {
        this.consultServise.getStudentInConsult()
            .subscribe(res => {
                console.log(res['students']);
                this.students = res['students'];
            });
    }


    onDelStudent(id) {
        this.consultServise.removeStudentFromConsult(id)
            .subscribe(res => {
                console.log(res);
                this.onUpdateStudentsList();
            });
    }

    onCloseConsult() {

        this.consultServise.closeCurrentConsult()
            .subscribe(res => {
                console.log(res);
                this.isOpen = false;
                this.init();

            });
    }
}






















