import {Injectable} from '@angular/core';
import {HttpClient, HttpParams} from '@angular/common/http';
import {Urls} from '../utils/urls';
import {map, tap} from 'rxjs/operators';
import {of} from 'rxjs';
import {Router} from '@angular/router';


@Injectable()
export class AuthService {

    constructor(private http: HttpClient, private router: Router) {
    }


    getToken() {
        const t = window.localStorage.getItem('token');
        return t ? t : null;
    }

    setToken(token) {
        window.localStorage.setItem('token', token);
    }

    clearToken() {
        window.localStorage.removeItem('token');
    }

    public register(login, pass) {
        const params = new HttpParams()
            .set('login', login)
            .set('pass', pass);
        const headers = {
            'Content-Type': 'application/x-www-form-urlencoded'
        };
        return this.http.post(Urls.url('auth/register'), params.toString(), {
            headers
        });
    }

    public login(login: string, pass: string) {
        const params = new HttpParams()
            .set('login', login)
            .set('pass', pass);
        const headers = {
            'Content-Type': 'application/x-www-form-urlencoded'
        };
        return this.http.post(Urls.url('auth/login'), params.toString(), {
            headers
        }).pipe(tap(res => {
            if (res['status'] === 'ok') {
                this.setToken(res['token']);
            }
        }))
            .pipe(
                map(res => res['status'] === 'ok')
            );
    }

    public isAuth() {
        return of(this.getToken() !== null);
    }

    public logout() {
        const params = new HttpParams()
            .set('token', this.getToken());
        const headers = {
            'Content-Type': 'application/x-www-form-urlencoded'
        };
        return this.http.post(Urls.url('auth/logout'), params.toString(), {
            headers
        }).pipe(
            map(res => res['status'] === 'ok')
        );
    }

    public invalidSession() {
        this.clearToken();
        this.router.navigate(['/auth']);

    }
}
